.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.iconsBtn:hover{
  background-color: #c5c5c5 !important;
}

.iconsBtn {
  transition: all 0.2s !important;
  box-shadow: 0px 7px rgba(0, 0, 0, 0.4);
}

.todoCard:hover{
  padding-bottom: 20px;
  padding-top: 20px;
}

.todoCard {
  transition: all 0.2s !important;
  box-shadow: 0px 7px rgba(0, 0, 0, 0.4);
}


@font-face {
  font-family: 'Regular';
  src: url('../public/Fonts/PlaywriteDEGrund-Regular.ttf') format('truetype');
}
@font-face {
  font-family: 'ExtraLight';
  src: url('../public/Fonts/PlaywriteDEGrund-ExtraLight.ttf') format('truetype');
}
@font-face {
  font-family: 'Light';
  src: url('../public/Fonts/PlaywriteDEGrund-Light.ttf') format('truetype');
}
@font-face {
  font-family: 'Thin';
  src: url('../public/Fonts/PlaywriteDEGrund-Thin.ttf') format('truetype');
}

